	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"/cats_story\">ネコでんせつ</a>の主人公、 “ミー” のイラスト。当時はキャラクタを縦横無尽に伸ばすのが好きだった。</p>\n<p>この頃 CATS STORY を RPG 化する計画を立てており、世界観滅茶苦茶な 1P ギャグ漫画から一本のストーリーを生み出す構想を立てていた。\n当時は全員頭が猫の猫人間で、シナリオも至極王動的な勧善懲悪物だった。</p>\n<p>後に人間と猫人間(獣人というよりも若干亜人っぽい描写になった)との対立を描いたダークなシナリオとなるが、それは一年半ほど後のお話。</p>","frontmatter":{"cover":null,"date":"1995-01-09T10:00:24+09:00","strDate":"1995/1/9","title":"ネコでんせつ"}}},"pathContext":{"path":"/ja/blog/1995/01/09-cats-story/","langKey":"ja"}}

